.user-registration{
  .box-card {
    box-shadow: 1px 3px 3px rgba(0, 0, 0, 0.06);
    border-radius: 5px;
    width: 100%;
    margin-top: 10px;

    .box-card-header {
      background: #F6F6F6;
      color: #474747;
      padding: 10px 30px;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
      text-align: right;

      .box-card-header-title {
        font-size: 15px;
        color: #404040;
        font-weight: bolder;
      }

      .box-card-header-description {
        font-size: 14px;
      }
    }


    .box-card-content {
      background: white;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;
      padding-bottom: 30px;
      .ant-switch-checked {
        margin-left: 10px;
      }

      .list-box {
        border: 0.5px solid #271E70;
        border-radius: 5px;
        min-height: 290px;
        width: 100%;
        padding: 20px;
        margin: 30px 12.5px 0px 12.5px;
        overflow-y: auto;

        @media screen and (max-width: 600px) {
          margin: 15px;
        }

        ul {
          padding-right: 0px;
          list-style-type: none;
          margin-bottom: 0px;
          .images {
            height: 150px;
            width: 100%;
            margin-bottom: 5px;
          }

          li {
            display: flex;
            justify-content: space-between;
            font-size: 12px;
            color: #271E70;
            font-weight: bold;
            border-bottom: 0.5px solid #271E70;
            min-height: 35px;
            align-items: flex-end;
            padding-top: 5px;
            position: relative;
            &:last-child{
              border-bottom: unset;
            }
            &:hover {
              cursor: pointer;
            }

            .form-control {
              border: 1px solid transparent;
              font-size: 12px;
              color: #271E70;
              font-weight: bold;
              padding-right: 0px;
              height: 25px;
              padding-bottom: 0px;
            }

            input::placeholder {
              font-size: 12px;
              color: #271E70;
            }

            .input-group-text {
              background-color: transparent;
              border: 1px solid transparent;
              padding-left: 0px;
            }

            .upload{
              top: 15px;
              text-align: center;
              position: absolute;
              right: 15px;
              left: 15px;
              padding: 10px;
              border-radius: 5px;
              background: rgba(255, 255, 255, 0.50);
              &:hover{
                cursor: pointer;
              }
            }

            .upload-img{
              width: 100%;
              border-radius: 5px;
              height: 220px;
            }
          }
        }

        .title {
          font-size: 12px;
          color: #271E70;
          font-weight: bold;
        }

        .form-control {
          border: 1px solid #271E70;
          border-radius: 5px;
        }
      }

      .list-box-plus {
        border: 0.5px dashed #271E70;
        border-radius: 5px;
        height: 322px;
        width: 100%;
        padding: 20px;
        margin: 30px 10px 0px 10px;
        display: flex;
        align-items: center;
        justify-content: center;
        @media screen and (max-width: 600px) {
          margin: 15px;
        }

        &:hover {
          cursor: pointer;
        }
      }
      .upload-box{
        border: 0.5px solid rgba(39, 30, 112, 0.50);
        border-radius: 5px;
        margin: 30px 15px 0px 15px;
        padding: 15px;
        .top{
          display: flex;
          height: 25px;
          align-items: center;
          color: rgba(39, 30, 112, 0.8);
          img{
            margin-left: 15px;
          }
        }

        .upload{
          width: 100%;
          border-radius: 5px;
          height: 180px;

          img{
            width: 100%;
            height: 100%;
            border-radius: 5px;
          }
        }
      }
      .inputs {
        display: flex;
        flex-direction: column;
        margin-top: 15px;
        padding: 0px 15px;

        .ant-select-selection--single .ant-select-selection__rendered {
          margin-right: 11px;
        }
        .ant-select-selection__rendered {
          margin-left: 24px;
        }
        .ant-select-arrow{
          left: 11px;
          right: unset;
        }
        .ant-select-selection__placeholder{
          right: 0px;
          left: 9px;
          text-align: right;
        }

        .ant-select-search__field__placeholder {
          right: 0px;
          left: 9px;
          text-align: right;
        }

        .ant-select-selection-selected-value{
          float: right;
        }
        .input-group-text {
          background-color: transparent;
          border: 1px solid transparent;
          font-size: 14px;
          font-family: IRANSans-web-bold;
          color: rgba(39, 30, 112, 0.47);
        }
        .input-group-text{
          background-color: transparent;
          border: 1px solid transparent;
          font-size: 14px;
          font-family: IRANSans-web-bold;
          color: rgba(39, 30, 112, 0.47);
        }
        label {
          font-size: 12px;
          color: #555555;
          font-family: IRANSans-web-bold;
        }

        input.form-control {
          height: 34px;
          border: 1px solid #8D8D8D;
          border-radius: 5px;
          background-color: #FBFBFB;
          color: #878585;
          font-size: 12px;
        }

        textarea.form-control {
          border: 1px solid #8D8D8D;
          border-radius: 5px;
          background-color: #FBFBFB;
          color: #878585;
          font-size: 12px;
        }
      }

      .description {
        color: rgba(39, 30, 112, 0.80);
        font-size: 14px;
        font-weight: bold;
        margin-top: 5px;
      }

      .more-details {
        font-family: IRANSans-web-bold;
        img {
          margin-left: 10px;
        }

        margin-top: 5px;
        margin-bottom: 10px;
        color: rgba(39, 30, 112, 0.80);
        font-size: 10px;
        font-weight: bold;
      }

    }
  }

  .small-profile{
    margin-top: 40px!important;
    height: 140px!important;
    @media screen and (max-width: 600px) {
      margin-top: 0px;
    }
  }
  .profile-avatar-2{
    box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.06);
    width: 100%;
    height: 180px;
    margin-top: 5px;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    img{
      border-radius: 5px;
      width: 100%;
      height: 100%;
    }

    .upload-2{
      position: absolute;
      background: #ebe9f9c7;
      bottom: 10px;
      padding: 5px 15px;
      border-radius: 5px;
      font-size: 12px;
      color: rgba(39, 30, 112, 0.8);
      span{
        margin-right: 5px;
      }
    }
  }

}

.role{
  font-size: 15px;
  color: #271E70;
  margin-right: 15px;
}
