.sidebar{
  box-shadow: 1px 2px 10px rgba(0, 0, 0, 0.10);
  background: white;
  border-radius: 5px;
  padding: 15px;
  text-align: right;
  list-style-type: none;
  margin-top: 10px;
  width: 100%;

    @media screen and (max-width: 768px) {
    display: none;
  }
  li{
    font-size: 14px;
    height: 40px;
    font-weight: bolder;
    img{
      margin-left: 10px;
    }
    @media screen and (max-width: 1060px) {
      font-size: 12px;
    }
    &:hover{
      cursor: pointer;
    }
  }
}
