.user-account {
  .box-card {
    box-shadow: 1px 3px 3px rgba(0, 0, 0, 0.06);
    border-radius: 5px;
    width: 100%;
    margin-top: 10px;

    .box-card-header {
      background: #F6F6F6;
      color: #474747;
      padding: 10px 30px;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
      text-align: right;
      position: relative;

      .box-card-header-title {
        font-size: 15px;
        color: #404040;
        font-weight: bolder;
      }

      .change{
        position: absolute;
        left: 30px;
        bottom: 15px;
        color: #B05CBA;
        font-size: 13px;
        &:hover{
          text-decoration: underline;
          cursor: pointer;
        }
      }
      .box-card-header-description {
        font-size: 14px;
      }
    }


    .box-card-content {
      background: white;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;
      padding-bottom: 30px;

      .ant-switch-checked {
        margin-left: 10px;
      }

      .inputs {
        display: flex;
        flex-direction: column;
        margin-top: 15px;
        padding: 0px 15px;

        .ant-select-selection--single .ant-select-selection__rendered {
          margin-right: 11px;
        }
        .ant-select-selection__rendered {
          margin-left: 24px;
        }
        .ant-select-arrow{
          left: 11px;
          right: unset;
        }
        .ant-select-selection__placeholder{
            right: 0px;
            left: 9px;
            text-align: right;
          }

        .ant-select-search__field__placeholder {
          right: 0px;
          left: 9px;
          text-align: right;
        }

        .ant-select-selection-selected-value{
          float: right;
        }
        .input-group-text {
          background-color: transparent;
          border: 1px solid transparent;
          font-size: 14px;
          font-family: IRANSans-web-bold;
          color: rgba(39, 30, 112, 0.47);
        }

        label {
          font-size: 12px;
          color: #555555;
          font-family: IRANSans-web-bold;
        }

        input.form-control {
          height: 34px;
          border: 1px solid #d9d9d9;
          border-radius: 5px;
          color: #878585;
          font-size: 12px;
        }

        textarea.form-control {
          border: 1px solid #8D8D8D;
          border-radius: 5px;
          background-color: #FBFBFB;
          color: #878585;
          font-size: 12px;
        }
      }

      .description {
        color: rgba(39, 30, 112, 0.80);
        font-size: 14px;
        font-weight: bold;
        margin-top: 5px;
      }

      .more-details {
        font-family: IRANSans-web-bold;

        img {
          margin-left: 10px;
        }

        margin-top: 5px;
        margin-bottom: 10px;
        color: rgba(39, 30, 112, 0.80);
        font-size: 10px;
        font-weight: bold;
      }

      .list-box {
        border: 0.5px solid #271E70;
        border-radius: 5px;
        min-height: 290px;
        width: 100%;
        padding: 20px;
        margin: 30px 12.5px 0px 12.5px;
        overflow-y: auto;

        @media screen and (max-width: 600px) {
          margin: 15px;
        }

        ul {
          padding-right: 0px;
          list-style-type: none;

          .images {
            height: 150px;
            width: 100%;
            margin-bottom: 5px;
          }

          li {
            display: flex;
            justify-content: space-between;
            font-size: 12px;
            color: #271E70;
            font-weight: bold;
            border-bottom: 0.5px solid #271E70;
            min-height: 35px;
            align-items: flex-end;
            //padding-bottom: 5px;
            padding-top: 5px;

            &:hover {
              cursor: pointer;
            }

            .form-control {
              border: 1px solid transparent;
              font-size: 12px;
              color: #271E70;
              font-weight: bold;
              padding-right: 0px;
              height: 25px;
              padding-bottom: 0px;
            }

            input::placeholder {
              font-size: 12px;
              color: #271E70;
            }

            .input-group-text {
              background-color: transparent;
              border: 1px solid transparent;
              padding-left: 0px;
            }
          }
        }

        .title {
          font-size: 12px;
          color: #271E70;
          font-weight: bold;
        }

        .form-control {
          border: 1px solid #271E70;
          border-radius: 5px;
        }
      }

      .list-box-plus {
        border: 0.5px dashed #271E70;
        border-radius: 5px;
        height: 290px;
        width: 100%;
        padding: 20px;
        margin: 30px 10px 0px 10px;
        display: flex;
        align-items: center;
        justify-content: center;
        @media screen and (max-width: 600px) {
          margin: 15px;
        }

        &:hover {
          cursor: pointer;
        }
      }

      .big-plus {
        border: 0.5px dashed #271E70;
        border-radius: 5px;
        width: 100%;
        padding: 20px;
        margin: 10px 30px 0px 30px;
        display: flex;
        align-items: center;
        justify-content: center;

        &:hover {
          cursor: pointer;
        }

        @media screen and (max-width: 600px) {
          margin: 0px 15px 15px 15px;

        }
      }

      .small-margin {
        padding: 10px !important;
      }

      .big-box {
        border: 0.5px solid #ced4da;
        border-radius: 5px;
        width: 100%;
        padding: 10px 30px 10px 30px;
        margin: 10px 30px 0px 30px;
        @media screen and (max-width: 600px) {
          margin: 15px;
        }


        input::placeholder {
          font-size: 14px;
          color: #271E70;
        }

        textarea::placeholder {
          font-size: 14px;
          color: #271E70;
        }

        textarea {
          margin-top: 10px;
        }
      }


    }
  }

  .Row {
    text-align: right;
  }

  .about-me {
    display: flex;
    align-items: baseline;
    margin-top: 30px;

    label {
      margin-left: 5px;
      font-size: 12px;
      color: #555555;
      font-family: IRANSans-web-bold;
    }

    .more-description {
      color: rgba(39, 30, 112, 0.8);
      font-size: 12px;
      margin-right: 5px;
      font-weight: bolder;
      font-family: IRANSans-web-bold;
    }
  }

  .social-btn {
    border: 1px solid #B05CBA;
    color: #B05CBA;
    border-radius: 5px;
    margin: 15px 0px 0px;
    height: 40px;
    padding: 0px 15px;
    font-size: 13px;
    font-weight: bolder;


    &:disabled{
      border: 1px solid rgba(176, 92, 186, 0.35);
      color: rgba(176, 92, 186, 0.35);
      cursor: no-drop;
    }
  }

  .save-btn {
    background: #46A8E1;
    color: white;
    height: 40px;
    text-align: center;
    border-radius: 5px;
    border: transparent;
    margin: 15px 0px 0px;
    padding: 0px 15px;
    font-size: 13px;
    font-weight: bolder;
  }

  .profile-avatar {
    //box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.06);
    width: 150px;
    height: 140px;
    margin-top: 10px;
    border-radius: 5px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    img{
      border-radius: 5px;
      width: 100%;
      height: 100%;
    }

    .upload{
      position: absolute;
      background: #eeeeeee8;
      bottom: 10px;
      padding: 5px 15px;
      border-radius: 18px;
      font-size: 10px;
      font-weight: bolder;
      span{
        margin-right: 5px;
      }
    }
  }

  .alert-box {
    background: rgb(213, 254, 199);
    color: rgb(43, 45, 41);
    padding: 10px;
    font-size: 11px;
    text-align: right;
    margin-top: 10px;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    font-weight: bolder;
  }



}

.modals {

  .modal-input {
    margin-bottom: 15px;

    .ant-input-group-wrapper {
      direction: ltr;
    }

    .ant-input-group-addon {
      background-color: #E8E8E8;
      border: 1px solid #E8E8E8;
      width: 130px;
    }

    .ant-input {
      border: 1px solid #E8E8E8;
    }

  }

  .save-btn {
    background: #46A8E1;
    color: white;
    height: 40px;
    text-align: center;
    border-radius: 4px;
    border: transparent;
    margin: 15px 0px 0px;
    padding: 0px 15px;
    font-size: 13px;
    font-weight: bolder;
  }

  .ant-modal-header{
    background: #F8F8F8;
    text-align: center;
  }
}
