.header{
  height: 71px;
  background: white;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.10);
  border-radius: 5px;
  margin-top: 5px;

  @media screen and (max-width: 1000px) {
    display: none;
  }
  .right{
    display: flex;
    align-items: center;
    height: 100%;
    padding-right: 15px;
    .avatar{
      width: 50px;
      height: 50px;
      border-radius: 50%;
      border: 1px solid #ddd;
    }

    .user-name{
      color: #222222;
      background: transparent;
      border: tan;
      font-size: 16px;
      @media screen and (max-width: 1212px) {
        font-size: 12px;
      }
      }

    .notification{
      position: relative;
      margin-right: 18px;
      @media screen and (max-width: 1212px) {
        margin-right: 5px;
      }
      .badge{
        position: absolute;
        background: #ED639E;
        color: white;
        font-size: 9px;
        padding: 3px 6px;
        left: 10px;
        top: -5px;
      }
    }

    .line{
      border-left: 1px solid #707070;
      height: 100%;
      margin: 0px 20px;
      @media screen and (max-width: 1212px) {
        margin: 0px 10px;

      }
      }


    .menu-item{
      display: flex;
      align-items: center;
      ul{
        list-style-type: none;
        padding-right: 0px;
        margin-bottom: 0px;
        display: flex;
        .active{
          background: #271E70;
          color: white;
          a{
            color: white;
          }
        }
        li{
          background: transparent;
          color: #271E70;
          height: 45px;
          font-size: 16px;
          padding: 10px 15px;
          text-align: center;
          border-radius: 5px;
          font-weight: bolder;
          img{
            margin-left: 10px;
          }
          @media screen and (max-width: 1212px) {
            font-size: 12px;
            padding: 10px;
            height: 40px;
          }
            &:hover{
            cursor: pointer;
          }
        }
      }
    }
  }

  .left{
    display: flex;
    align-items: center;
    height: 100%;
    flex-direction: row-reverse;
    padding-left: 15px;
    @media screen and (max-width: 1212px) {
      font-size: 9px;
    }
      .logo{
      margin-right: 10px;
    }

    .line{
      border-left: 1px solid #707070;
      height: 100%;
      margin: 0px 20px;
      @media screen and (max-width: 1212px) {
        margin: 0px 10px;
      }
    }

    .search{
      .input-group-text{
        background-color: #FCFCFC;
        border-bottom-left-radius: 25px;
        border-top-left-radius: 25px;
        height: 40px;
      }
      .form-control{
        background-color: #FCFCFC;
        border-top-right-radius: 25px;
        border-bottom-right-radius: 25px;
        border-left: none;
        height: 40px;
      }
    }
  }

  .inputs{

  }

}

.register-modal{
  .btn-save{
    background: #46A8E1;
    color: white;
    width: 100%;
    height: 50px;
    border-radius: 5px;
    border: transparent;
    font-size: 20px;
    margin-top: 10px;
    font-weight: bolder;
    font-family: IRANSans-web-bold;
  }
  .btn-change{
    background: transparent;
    color: #46A8E1;
    width: 100%;
    height: 45px;
    border-radius: 5px;
    border: transparent;
    font-size: 18px;
    margin-top: 10px;
    border: 2px solid #46A8E1;
  }
  .re-capture{
    background: #eee;
    border: 0.5px solid rgba(39, 30, 112, 0.5);
    border-radius: 5px;
    height: 63px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: rgba(39, 30, 112, 0.5);
    margin-top: 10px;
    font-size: 16px;
    font-weight: bolder;
  }
  .gender{
    text-align: right;
    margin-top: 15px;
    color: #271E70;
    font-size: 13px;
  }
  .change{
    margin-bottom: 10px;
    color: rgba(39, 30, 112, 0.47);
    font-size: 14px;
  }
  .or-line {
    hr.or {
      border-top-color: rgba(39, 30, 112, 0.5);
      border-top-style: solid;
      display: flex;
      justify-content: center;
      margin-top: 30px;
      margin-bottom: 30px;
    }

    .or-text{
      height: 55px;
      background: white;
      width: 55px;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      position: absolute;
      top: -26px;
      right: 46%;
      font-weight: bolder;
      font-size: 20px;
      color: rgba(39, 30, 112, 0.47);
    }
  }

  .google{
    border: 0.5px solid rgba(39, 30, 112, 0.5);
    height: 50px;
    margin-top: 10px;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    font-weight: bold;
    color: rgba(64, 64, 64, 0.90);
    font-family: IRANSans-web-bold;

    img{
      width: 25px;
      margin-left: 15px;
    }
  }

  .login-text{
    color: rgba(39, 30, 112, 0.50);
    font-size: 16px;
    margin-top: 15px;
    font-weight:bolder;
    font-family: IRANSans-web-bold;
  }
}
.ant-menu-item{
