    margin-top: 5px;

    .right{
      display: flex;
      align-items: center;
      height: 100%;
      padding-right: 5px;
      font-size: 12px;
      .logo{
        margin-left: 10px;
        width: 42px;
      }
    }

    .left{
      display: flex;
      align-items: center;
      height: 100%;
      flex-direction: row-reverse;
      padding-left: 10px;
      .avatar{
        width: 35px;
        height: 35px;
        border-radius: 50%;
        border: 1px solid #ddd;
      }

      .user-name{
        color: #222222;
        background: transparent;
        border: tan;
        font-size: 12px;
      }

      .notification{
        position: relative;
        .badge{
          position: absolute;
          background: #ED639E;
          color: white;
          font-size: 9px;
          padding: 3px 6px;
          left: 10px;
          top: -5px;
        }
      }
    }
  }
}


.header-modals{
  .ant-modal-header{
    border-bottom: 0px;
    text-align: right;
    padding: 30px 37px 0px;
    color: #271E70;
    .ant-modal-title {
      color: #271E70;
      font-size: 28px;
      font-family: IRANSans-web-bold;
    }
  }

  .ant-modal-body {
    padding: 24px 24px 40px;
  }
  .inputs{
    position: relative;
    label{
      color: #271E70;
      font-size: 13px;
      margin-top: 15px;
    }
    .timer{
      position: absolute;
      top: 15px;
      left: 0px;
      color: #271E70;
      font-size: 13px;
    }

    .description{
      color: rgba(39, 30, 112, 0.70);
      font-size: 13px;
    }
    input::placeholder{
      color: rgba(39, 30, 112, 0.50);
      font-size: 12px;
    }
  }

  .alert-text{
    text-align: right;
    color: #ED639E;
    font-size: 12px;
    span{
      margin-right: 10px;
    }
  }
  .ant-modal-close{
    left: 0px;
    right: unset;
  }
}
